}
header .setting {
  display: flex;
  justify-content: flex-end;
  padding: 10px;
  border-bottom: 2px solid black;
  margin-bottom: 20px;
}
header .setting a {
  text-decoration: none;
  font-size: 14px;
  font-weight: 500;
  text-align: right;
  display: block;
  margin: 0px 5px;
  text-transform: capitalize;
  transition: all 500ms;
}
header .setting a:hover {
  color: #f49d37;
}
header .setting a:active {
  color: #f49d37;
}
header .navbar {
  width: 100%;
}
header .navbar .search form {
  display: flex;
}
@media (max-width: 991px) {
  header .navbar .search form {
    margin-top: 20px;
  }
}
@media (max-width: 1199px) {
  header .navbar .search form {
    margin-top: 0;
  }
}
header .navbar .search form input {
  width: 100%;
  border: 2px solid #00467a;
  color: #00467a;
  margin: 0px 5px;
  font-size: 14px;
  font-weight: 500;
  padding: 10px 15px;
  border-radius: 5px;
}
header .navbar .search form input:focus-visible {
  outline: none;
}
header .navbar .search form button {
  border-color: #00467a;
  color: #00467a;
  padding: 0px 10px;
}
@media (max-width: 991px) {
  header .navbar .shopBags {
    margin-right: 15px;
  }
}
@media (max-width: 1199px) {
  header .navbar .shopBags {
    margin-right: 0px;
  }
}
header .navbar .shopBags a {
  position: relative;
  text-decoration: none;
  font-size: 20px;
  font-weight: 700;
  transition: all 500ms;
  color: #00467a;
}
@media (max-width: 1199px) {
  header .navbar .shopBags a {
    font-size: 16px;
    font-weight: 500;
  }
}
header .navbar .shopBags a:hover {
  color: #f49d37;
}
header .navbar .shopBags a .fas {
  position: relative;
  text-align: center;
  display: block;
  font-size: 25px;
  font-weight: 500;
  margin-bottom: 5px;
}
@media (max-width: 1199px) {
  header .navbar .shopBags a .fas {
    font-size: 23px;
  }
}
header .navbar .shopBags a .fas .count {
  position: absolute;
  top: -20px;
  right: 10px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-size: 13px;
  font-weight: 700;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  color: white;
  background: #00467a;
  text-align: center;
}
@media (max-width: 1199px) {
  header .navbar .shopBags a .fas .count {
    top: -19px;
    width: 22px;
    height: 22px;
    font-weight: 500;
  }
}
header .navbar .shopBags a span {
  display: block;
  text-align: center;
}
header .navbar .phone {
  text-align: right;
}
header .navbar .phone a {
  font-size: 18px;
  color: #00467a;
  font-weight: 700;
  letter-spacing: 10px;
  text-decoration: none;
  transition: all 500ms;
}
@media (max-width: 1400px) {
  header .navbar .phone a {
    font-size: 16px;
  }
}
@media (max-width: 1400px) {
  header .navbar .phone a {
    letter-spacing: 7px;
  }
}
@media (max-width: 1199px) {
  header .navbar .phone a {
    letter-spacing: 3px;
  }
}
@media (max-width: 991px) {
  header .navbar .phone a {
    font-size: 20px;
    letter-spacing: 4px;
  }
}
header .navbar .phone a:hover {
  color: #f49d37;
}
@media (max-width: 1199px) {
  header .navbar .nav-flex {
    flex-wrap: nowrap;
  }
}
@media (max-width: 991px) {
  header .navbar .nav-flex {
    flex-wrap: wrap;
  }
}
header .header-menubar .nav-menu {
  margin-top: 30px;
  background: #00467a;
  padding: 10px 0px;
  box-shadow: 2px 0px 4px black;
  margin-bottom: 30px;
}
header .header-menubar .nav-menu .nav {
  display: flex;
  align-items: center;
}
header .header-menubar .nav-menu .nav li {
  margin: 0px 10px;
}
header .header-menubar .nav-menu .nav li .link {
  text-decoration: none;
  font-size: 16px;
  font-weight: 500;
  color: white;
  transition: all 100ms;
  display: block;
  padding: 5px 0px;
  overflow: hidden;
  position: relative;
  transition: all 500ms;
}
header .header-menubar .nav-menu .nav li .link:hover {
  color: #f49d37;
}
header .header-menubar .nav-menu .nav li .sort {
  text-decoration: none;
  font-size: 16px;
  font-weight: 500;
  color: #f49d37;
  padding: 5px 0px;
  display: flex;
  align-items: center;
}
header .header-menubar .nav-menu .nav li .sort .fas {
  margin-left: 5px;
  padding-bottom: 5px;
}
header .header-menubar .nav-menu-list {
  display: none;
  position: relative;
}
header .header-menubar .nav-menu-list ul {
  padding: 0px 10px;
  background: white;
  width: 100%;
  position: absolute;
  top: -30px;
  left: 0;
  z-index: 1110;
  border: 2px solid #adadad;
}
header .header-menubar .nav-menu-list ul li {
  padding: 10px;
  display: flex;
  align-items: center;
}
header .header-menubar .nav-menu-list ul li::before {
  content: "";
  display: block;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: #00467a;
  margin-right: 10px;
}
header .header-menubar .nav-menu-list ul li a {
  text-decoration: none;
}
header .scroll-top {
  position: fixed;
  top: 600px;
  right: 100px;
  background: #00467a;
  padding: 10px;
  border-radius: 50%;
  display: none;
  z-index: 11111;
}
header .scroll-top a {
  color: white;
  text-decoration: none;
  font-size: 14px;
  font-weight: 700;
}
header .owl-carousel .owl-stage-outer .owl-stage .owl-item .hr_item {
  position: relative;
}
header .owl-carousel .owl-stage-outer .owl-stage .owl-item .hr_item .hr-item-text {
  position: absolute;
  width: 30%;
  height: 30%;
  background: rgba(0, 0, 0, 0.4);
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 30px;
  border: 6px solid #00467a;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
@media (max-width: 1199px) {
  header .owl-carousel .owl-stage-outer .owl-stage .owl-item .hr_item .hr-item-text {
    width: 50%;
  }
}
@media (max-width: 1199px) {
  header .owl-carousel .owl-stage-outer .owl-stage .owl-item .hr_item .hr-item-text {
    height: 40%;
  }
}
header .owl-carousel .owl-stage-outer .owl-stage .owl-item .hr_item .hr-item-text h2 {
  font-size: 30px;
  font-weight: 700;
  color: white;
  text-align: center;
  font-family: "etelkBold";
  margin-bottom: 20px;
}
@media (max-width: 1199px) {
  header .owl-carousel .owl-stage-outer .owl-stage .owl-item .hr_item .hr-item-text h2 {
    font-size: 28px;
  }
}
header .owl-carousel .owl-stage-outer .owl-stage .owl-item .hr_item .hr-item-text .tel-carousel {
  display: inline-block;
  text-align: center;
  margin: auto;
  text-decoration: none;
  font-size: 25px;
  font-weight: 100;
  font-family: "etelkLight";
  color: #00467a;
  background: #f49d37;
  padding: 15px 25px;
  border-radius: 30px;
  transition: all 500ms;
}
@media (max-width: 1199px) {
  header .owl-carousel .owl-stage-outer .owl-stage .owl-item .hr_item .hr-item-text .tel-carousel {
    font-size: 20px;
  }
}
header .owl-carousel .owl-stage-outer .owl-stage .owl-item .hr_item .hr-item-text .tel-carousel:hover {
  color: #00467a;
  transform: scale(1.1);
}
header .owl-carousel .owl-nav button {
  background: transparent;
  border: none;
  font-size: 60px;
  font-weight: 700;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #f49d37;
}
header .owl-carousel .owl-nav .owl-prev {
  position: absolute;
  top: 50%;
  left: 80px;
  transform: translateY(-50%);
}
header .owl-carousel .owl-nav .owl-next {
  position: absolute;
  top: 50%;
  right: 80px;
  transform: translateY(-50%);
}
header .owl-carousel .owl-dots {
  position: absolute;
  bottom: 10px;
  right: 43%;
  transform: translate(-50%, -50%);
}
@media (max-width: 1199px) {
  header .owl-carousel .owl-dots {
    right: 38%;
  }
}
header .owl-carousel .owl-dots .owl-dot {
  width: 20px;
  height: 20px;
  background: #fff;
  border: 2px solid #f49d37;
  margin: 0px 10px;
  border-radius: 50%;
}
header .owl-carousel .owl-dots .owl-dot.active {
  background: #f49d37;
}
header .owl-carousel img {
  width: 100%;
  height: 800px;
}
@media (max-width: 1199px) {
  header .owl-carousel img {
    height: 550px;
  }
}
section {
