main {
  margin-top: 80px;
}
main .owl-carousel {
  margin-top: 50px;
  position: relative;
}
main .owl-carousel .owl-dot {
  display: none;
}
main .owl-carousel .owl-nav button {
  background: transparent;
  border: none;
  font-size: 70px;
  font-weight: 700;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #f49d37;
}
main .owl-carousel .owl-nav .owl-prev {
  position: absolute;
  top: 20%;
  left: -5%;
}
main .owl-carousel .owl-nav .owl-next {
  position: absolute;
  top: 20%;
  right: -5%;
}
main .owl-carousel .img_product {
  max-width: 200px;
  height: 300px;
  position: relative;
}
main .owl-carousel .img_product img {
  border: 2px solid #9b9b9b;
  padding: 10px;
}
main .owl-carousel .img_product .text-carousel {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 200px;
  background: rgba(0, 22, 117, 0.7);
  opacity: 0;
  color: white;
  font-size: 16px;
  font-weight: 700;
}
main .owl-carousel .img_product:hover .text-carousel {
  transition: 500ms;
  opacity: 1;
}
*:hover {
  cursor: pointer;
}
img {
  max-width: 100%;
  height: auto;
}
body {
  min-width: 320px;
  font-family: "etelkReg";
  text-decoration: none;
}
.btn {
  border: none;
  background: transparent;
  color: #f49d37;
  border: 2px solid #f49d37;
  border-radius: 5px;
}
h2 {
  font-size: 35px;
  font-weight: 700;
  text-transform: capitalize;
  border-bottom: 6px solid #f49d37;
  color: #f49d37;
  padding: 15px 0px;
  margin-bottom: 50px;
  font-family: 'etelkBold';
}
.header-nav {
  max-width: 1500px;
  margin: auto;
}
.header-nav .nav-flex {
  display: flex;
  flex-wrap: wrap;
}
.header-nav .nav-col-3 {
  width: calc(100% / 12 * 3);
  height: auto;
  margin: 0px 15px;
}
.header-nav .nav-col-2 {
  width: calc(100% / 12 * 2);
  height: auto;
  margin: 0px 15px;
}
.header-nav .nav-col-1 {
  width: calc(100% / 12 * 1);
  height: auto;
  margin: 0px 15px;
}
.header-nav .nav-col-6 {
  width: calc(100% / 12 * 6);
  height: auto;
  margin: 0px 15px;
}
.header-nav .nav-col-7 {
  width: calc(100% / 12 * 7);
  height: auto;
  margin: 0px 15px;
}
@media (max-width: 1199px) {
  .nav-lg-col-2 {
    width: calc(100% / 12 * 2) !important;
    height: auto;
    margin: 0px 15px;
  }
}
@media (max-width: 1200px) {
  .nav-xl-col-1 {
    width: calc(100% / 12 * 1);
    height: auto;
    margin: 0px 15px;
  }
}
@media (max-width: 991px) {
  .nav-md-col-12 {
    width: calc(100% / 12 * 12) !important;
    height: auto;
    margin: 0px 15px;
  }
}
@media (max-width: 991px) {
  .nav-md-col-5 {
    width: calc(100% / 12 * 5) !important;
    height: auto;
    margin: 0px 15px;
  }
}
@media (max-width: 991px) {
  .nav-md-col-8 {
    width: calc(100% / 12 * 8) !important;
    height: auto;
    margin: 0px 15px;
  }
}
@media (max-width: 767px) {
  .nav-sm-col-4 {
    width: calc(100% / 12 * 4) !important;
    height: auto;
    margin: 0px 15px;
  }
}
@media (max-width: 767px) {
  .nav-sm-col-12 {
    width: calc(100% / 12 * 12) !important;
    height: auto;
    margin: 0px 15px;
  }
}
