footer{
    width: 100%;
    margin-top: 100px;
    padding-top: 40px;
    background: #00467a;
    .contact-info {
      text-align: center;
      h3 {
        font-size: 24px;
        font-weight: 700;
        font-family: "etelkBold";
        margin-bottom: 20px;
        text-align: center;
        color: #f49d37;
      }
      p{
        color: #fff;
        font-size: 18px;
        font-family: "etelkLight";
        font-weight: 100;
        text-align: left;
      }
      a {
        text-decoration: none;
        font-size: 18px;
        font-family: "etelkLight";
        font-weight: 100;
        text-align: center;
        display: inline-block;
        color: #fff;
        &:hover {
          transition: all 500ms;
          color: #f49d37;
          transform: scale(1.1);
        }
      }
      .instagram {
        margin: 0px 15px;
      }
    }
    .contact{
      margin-top: 40px;
    }
}