/* ================== header_starting ==================  */
header {
  /* control-panel  */
  .setting {
    display: flex;
    justify-content: flex-end;
    padding: 10px;
    border-bottom: 2px solid black;
    margin-bottom: 20px;
    a {
      text-decoration: none;
      font-size: 14px;
      font-weight: 500;
      text-align: right;
      display: block;
      margin: 0px 5px;
      text-transform: capitalize;
      transition: all 500ms;
      &:hover {
        color: #f49d37;
      }
      &:active {
        color: #f49d37;
      }
    }
  }

  /* navbar-menu  */
  .navbar {
    width: 100%;
    .search {
      form {
        display: flex;
        .md(margin-top, 20px);
        .lg(margin-top , 0);
        input {
          width: 100%;
          border: 2px solid #00467a;
          color: #00467a;
          margin: 0px 5px;
          font-size: 14px;
          font-weight: 500;
          padding: 10px 15px;
          border-radius: 5px;
          &:focus-visible {
            outline: none;
          }
        }
        button {
          border-color: #00467a;
          color: #00467a;
          padding: 0px 10px;
        }
      }
    }
    .shopBags {
      .md(margin-right, 15px);
      .lg(margin-right, 0px);
      a {
        position: relative;
        text-decoration: none;
        font-size: 20px;
        font-weight: 700;
        transition: all 500ms;
        .lg-block({
          font-size: 16px;
          font-weight: 500;
        });
        &:hover {
          color: #f49d37;
        }
        .fas {
          position: relative;
          text-align: center;
          display: block;
          font-size: 25px;
          font-weight: 500;
          margin-bottom: 5px;
          .lg-block({
            font-size: 23px;
          });
          .count {
            position: absolute;
            top: -20px;
            right: 10px;
            display: inline-flex;
            align-items: center;
            justify-content: center;
            font-size: 13px;
            font-weight: 700;
            width: 25px;
            height: 25px;
            text-align: center;
            border-radius: 50%;
            color: white;
            background: #00467a;
            text-align: center;
            .lg-block({
              top: -19px;
              width: 22px;
              height: 22px;
              font-weight: 500;
            });
          }
        }

        span {
          display: block;
          text-align: center;
        }
        color: #00467a;
      }
    }
    .phone {
      text-align: right;

      a {
        font-size: 18px;
        color: #00467a;
        font-weight: 700;
        letter-spacing: 10px;
        text-decoration: none;
        transition: all 500ms;
        .media(font-size , 16px , 1400px);
        .media(letter-spacing , 7px , 1400px);
        .lg(letter-spacing , 3px);
        .md-block({
          font-size: 20px;
          letter-spacing: 4px;
        });
        &:hover {
          color: #f49d37;
        }
      
      }
    }
    .nav-flex {
      .lg-block({
        flex-wrap: nowrap;
      });
      .md(flex-wrap , wrap);
    }
  }

  /* header-menubar  */
  .header-menubar {
    .nav-menu {
      margin-top: 30px;
      background: #00467a;
      padding: 10px 0px;
      box-shadow: 2px 0px 4px black;
      margin-bottom: 30px;
      .nav {
        display: flex;
        align-items: center;
        li {
          margin: 0px 10px;
          .link {
            text-decoration: none;
            font-size: 16px;
            font-weight: 500;
            color: white;
            transition: all 100ms;
            display: block;
            padding: 5px 0px;
            overflow: hidden;
            position: relative;
            transition: all 500ms;
            &:hover {
              color: #f49d37;
            }
          }
          .sort {
            text-decoration: none;
            font-size: 16px;
            font-weight: 500;
            color: #f49d37;
            padding: 5px 0px;
            display: flex;
            align-items: center;
            .fas {
              margin-left: 5px;
              padding-bottom: 5px;
            }
          }
        }
      }
    }
    .nav-menu-list {
      display: none;
      position: relative;
      ul {
        padding: 0px 10px;
        background: white;
        width: 100%;
        position: absolute;
        top: -30px;
        left: 0;
        z-index: 1110;
        border: 2px solid rgb(173, 173, 173);

        li {
          padding: 10px;
          display: flex;
          align-items: center;

          &::before {
            content: "";
            display: block;
            width: 10px;
            height: 10px;
            border-radius: 50%;
            background: #00467a;
            margin-right: 10px;
          }

          a {
            text-decoration: none;
          }
        }
      }
    }
  }
  /* scroll-top  */
  .scroll-top {
    position: fixed;
    top: 600px;
    right: 100px;
    background: #00467a;
    padding: 10px;
    border-radius: 50%;
    display: none;
    z-index: 11111;
    a {
      color: white;
      text-decoration: none;
      font-size: 14px;
      font-weight: 700;
    }
  }
  /* owl-carousel  */
  .owl-carousel {
    .owl-stage-outer {
      .owl-stage {
        .owl-item {
          .hr_item {
            position: relative;
            .hr-item-text {
              position: absolute;
              width: 30%;
              height: 30%;
              background: rgba(0, 0, 0, 0.4);
              top: 50%;
              left: 50%;
              transform: translate(-50%, -50%);
              padding: 30px;
              border: 6px solid #00467a;
              display: flex;
              flex-direction: column;
              justify-content: center;
              align-items: center;
              .lg(width , 50%);
              .lg(height , 40%);

              h2 {
                font-size: 30px;
                font-weight: 700;
                color: white;
                text-align: center;
                font-family: "etelkBold";
                margin-bottom: 20px;
                .lg(font-size , 28px);
              }
              .tel-carousel {
                display: inline-block;
                text-align: center;
                margin: auto;
                text-decoration: none;
                font-size: 25px;
                font-weight: 100;
                font-family: "etelkLight";
                color: #00467a;
                background: #f49d37;
                padding: 15px 25px;
                border-radius: 30px;
                transition: all 500ms;
                .lg(font-size , 20px);
                &:hover {
                  color: #00467a;
                  transform: scale(1.1);
                }
              }
            }
          }
        }
      }
    }
    .owl-nav {
      button {
        background: transparent;
        border: none;
        font-size: 60px;
        font-weight: 700;
        display: flex;
        justify-content: center;
        align-items: center;
        color: #f49d37;
      }
      .owl-prev {
        position: absolute;
        top: 50%;
        left: 80px;
        transform: translateY(-50%);
      }
      .owl-next {
        position: absolute;
        top: 50%;
        right: 80px;
        transform: translateY(-50%);
      }
    }
    .owl-dots {
      position: absolute;
      bottom: 10px;
      right: 43%;
      transform: translate(-50%, -50%);
      .lg(right , 38%);
      .owl-dot {
        width: 20px;
        height: 20px;
        background: #fff;
        border: 2px solid #f49d37;
        margin: 0px 10px;
        border-radius: 50%;
      }
      .owl-dot.active {
        background: #f49d37;
      }
    }
    img {
      width: 100%;
      height: 800px;
      .lg(height , 550px);
    }
  }
}

