main {
  margin-top: 80px;
  .owl-carousel {
    margin-top: 50px;
    position: relative;
    .owl-dot {
      display: none;
    }
    .owl-nav {
      button {
        background: transparent;
        border: none;
        font-size: 70px;
        font-weight: 700;
        display: flex;
        justify-content: center;
        align-items: center;
        color: #f49d37;
      }
      .owl-prev {
        position: absolute;
        top: 20%;
        left: -5%;
      }
      .owl-next {
        position: absolute;
        top: 20%;
        right: -5%;
      }
    }
    .img_product {
      max-width: 200px;
      height: 300px;
      position: relative;
      img {
        border: 2px solid rgb(155, 155, 155);
        padding: 10px;
      }
      .text-carousel {
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 200px;
        background: rgba(0, 22, 117, 0.7);
        opacity: 0;
        color: white;
        font-size: 16px;
        font-weight: 700;
      }
      &:hover .text-carousel {
        transition: 500ms;
        opacity: 1;
      }
    }
  }
}
